
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}






























































































































































































































/* シェアボタン */
.share {
  margin-top: 40px;
  clear: both;
}
.share ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.share li a {
  display: block;
  text-align: center;
  padding: 10px 5px;
  color: #ffffff;
  font-size: 12px;
  text-decoration: none;
}
.share li a:hover {
  opacity: 0.8;
}
.share ul:after {
  content: "";
  display: block;
  clear: both;
}
.share li {
  float: left;
  width: 25%;
}
.share-tw {
  background-color: #55acee;
}
.share-fb {
  background-color: #3b5998;
}
.share-gp {
  background-color: #dd4b39;
}
.share-hb {
  background-color: #2c6ebd;
}
.hatenaB:before {
  content: 'B!';
  font-style: normal;
}
@media (max-width: 599px) {
.share span {
    display: inline-block;
    text-indent: -9999px;
}
.share li a {
    padding: 8px 10px;
}
}
















